import { Button, FormControl, FormControlLabel, Paper, Radio, RadioGroup } from "@material-ui/core";
import React, { useState } from "react";
import { toast } from "react-toastify";
import { IQuestion } from "../interfaces/IQuestion";
import ParsedText from "./ParsedText";

interface QuestionProps extends IQuestion {
    next: (answer: string) => void;
    prev: () => void;
}
const Question = ({ question, answers, correct, next, prev }: QuestionProps) => {
    const [radioState, setRadioState] = useState<string>("");
    const [checked, setChecked] = useState<boolean>(false);

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!checked) setRadioState(event.target.value);
    };

    return (
        <div className="question">
            <Paper elevation={3}>
                <h2>Frage</h2>
                <ParsedText>{question}</ParsedText>
            </Paper>
            <Paper elevation={3}>
                <h2>Antworten</h2>
                {answers.map((answer, index) => (
                    <div
                        className={
                            "answers " +
                            (checked
                                ? correct === index
                                    ? "correct"
                                    : radioState === String(index)
                                    ? "false"
                                    : ""
                                : "")
                        }
                        key={index}
                    >
                        <Radio checked={radioState === String(index)} value={index} onChange={handleChange} />

                        <ParsedText>{answer}</ParsedText>
                    </div>
                ))}
            </Paper>
            <Paper elevation={3}>
                <div className="questionNavi">
                    <Button
                        variant="contained"
                        onClick={() => {
                            prev();
                        }}
                    >
                        Zurück
                    </Button>
                    {checked ? (
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => {
                                next(radioState);
                            }}
                        >
                            Weiter
                        </Button>
                    ) : (
                        <div
                            onClick={() => {
                                if (radioState === "") toast.warning("Du hast keine Antwort ausgewählt!");
                            }}
                        >
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={() => {
                                    setChecked(true);
                                }}
                                disabled={radioState === ""}
                            >
                                Überprüfen
                            </Button>
                        </div>
                    )}
                </div>
            </Paper>
        </div>
    );
};
export default Question;
