import { Paper } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { GivenAnswers } from "../interfaces/IGivenAnswer";
import { IQuestion } from "../interfaces/IQuestion";
import { generateTest } from "../util/generateTest";
import { shuffleAnswers } from "../util/shuffleAnswers";
import Question from "./Question";
import Result from "./Result";

interface TestProps {
    count: number;
    questions: IQuestion[];
    resetTest: () => void;
}

const Test = (props: TestProps) => {
    const [active, setActive] = useState<number>(0);
    const [givenAnswers, setGivenAnswers] = useState<GivenAnswers[]>([]);

    const [test, setTest] = useState<IQuestion[]>([]);

    useEffect(() => {
        setTest(generateTest(props.questions, props.count));
    }, [props.questions, props.count]);

    return (
        <div>
            {active < props.count ? (
                <>
                    <Paper elevation={3}>
                        <div>
                            {active + 1} von {props.count}
                        </div>
                    </Paper>
                    {test[active] && (
                        <Question
                            key={active}
                            question={test[active]?.question}
                            answers={test[active]?.answers}
                            correct={test[active]?.correct}
                            next={(answer: string) => {
                                setGivenAnswers([
                                    ...givenAnswers,
                                    { given: answer, questionIndex: active, correct: test[active]?.correct },
                                ]);
                                setActive(active + 1);
                            }}
                            prev={() => setActive(active - 1 >= 0 ? active - 1 : 0)}
                        ></Question>
                    )}
                </>
            ) : (
                <Result givenAnswers={givenAnswers} questions={test} resetTest={props.resetTest} />
            )}
        </div>
    );
};

export default Test;
