import { Accordion, AccordionDetails, AccordionSummary, Button, Paper, Typography } from "@material-ui/core";
import React from "react";
import { GivenAnswers } from "../interfaces/IGivenAnswer";
import { IQuestion } from "../interfaces/IQuestion";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ParsedText from "./ParsedText";

interface ResultProps {
    questions: IQuestion[];
    givenAnswers: GivenAnswers[];
    resetTest: () => void;
}
const Result = (props: ResultProps) => {
    let correctAnswers = 0;
    let wrongAnswers = 0;

    props.givenAnswers.forEach((item) => {
        if (String(item.correct) == item.given) correctAnswers++;
        else wrongAnswers++;
    });

    const correctPercent = Math.round((correctAnswers / (correctAnswers + wrongAnswers)) * 100);
    return (
        <div className="result">
            <Paper elevation={3}>
                <h1>Ergebnis</h1>
                <div className="percentage">
                    <span className={"percentageNumber" + (correctPercent >= 50 ? " good" : " bad")}>
                        {correctPercent}%
                    </span>
                    <span> der Fragen wurden richtig beantwortet</span>
                </div>
                <div className="count">
                    <span className="correctCount">{correctAnswers}</span>
                    <span>Frage korrekt beantwortet</span>
                </div>
                <div className="count">
                    <span className="falseCount">{wrongAnswers}</span>
                    <span>Frage falsch beantwortet</span>
                </div>
            </Paper>

            <div className="repeat">
                {props.givenAnswers.map((answer) => {
                    return (
                        <div key={answer.questionIndex}>
                            <Accordion
                                className={
                                    "restultQuestion " + (String(answer.correct) === answer.given ? "correct" : "false")
                                }
                            >
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel1a-content"
                                    id="panel1a-header"
                                >
                                    <Typography>
                                        {props.questions[answer.questionIndex].question.substring(0, 37)}
                                        {props.questions[answer.questionIndex].question.length > 37 ? "..." : ""}
                                    </Typography>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <div className="details">
                                        <h2>Frage</h2>
                                        <ParsedText>{props.questions[answer.questionIndex].question}</ParsedText>

                                        {!isNaN(parseInt(answer.given)) && (
                                            <>
                                                <h2>Deine Antwort</h2>
                                                <ParsedText>
                                                    {
                                                        props.questions[answer.questionIndex].answers[
                                                            parseInt(answer.given)
                                                        ]
                                                    }
                                                </ParsedText>
                                            </>
                                        )}
                                        <h2>Korrekte Antwort</h2>
                                        <ParsedText>
                                            {props.questions[answer.questionIndex].answers[answer.correct]}
                                        </ParsedText>
                                    </div>
                                </AccordionDetails>
                            </Accordion>
                        </div>
                    );
                })}
            </div>
            <Paper elevation={3}>
                <Button variant="contained" color="primary" onClick={() => props.resetTest()}>
                    Nochmal
                </Button>
            </Paper>
        </div>
    );
};

export default Result;
