import React, { useState } from "react";
import "./App.css";

import chemie from "./questions/chemie.json";
import Test from "./components/Test";
import { AppBar, Button, IconButton, Paper, TextField, Toolbar, Typography } from "@material-ui/core";
import { ToastContainer, toast } from "react-toastify";
import { IQuestion } from "./interfaces/IQuestion";
import "react-toastify/dist/ReactToastify.css";

function App() {
    const [count, setCount] = useState<number>(10);
    const [fileName, setFileName] = useState<string>("Datei auswählen");
    const [questions, setQuestions] = useState<IQuestion[]>([]);
    const [hasTestStarted, setTestStarted] = useState<boolean>();

    const importJSON = () => {
        const inputField: any = document.getElementById("file-upload");
        var files = inputField?.files;

        if (files.length <= 0) {
            return false;
        }
        if (files && files[0]) setFileName(files[0].name);

        var fr = new FileReader();

        fr.onload = function (e: any) {
            try {
                var result = JSON.parse(e.target.result);
                setQuestions(result);
                toast.success("Es wurden " + result.length + " Fragen erfolgreich importiert!");
            } catch (error) {
                toast.error(
                    "Beim importieren der Fragen ist ein Fehler aufgetreten. Bitte überprüfe deine .json Datei"
                );
            }
        };

        fr.readAsText(files.item(0));
    };

    return (
        <div>
            <AppBar position="static">
                <div onClick={() => setTestStarted(false)} style={{ cursor: "pointer" }}>
                    <Toolbar>
                        <Typography variant="h6">JsonLearn</Typography>
                    </Toolbar>
                </div>
            </AppBar>
            <ToastContainer
                position="bottom-center"
                autoClose={6000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />
            <div className="App">
                {hasTestStarted ? (
                    <Test
                        key={fileName + count}
                        questions={questions}
                        count={count}
                        resetTest={() => setTestStarted(false)}
                    ></Test>
                ) : (
                    <>
                        <Paper elevation={3}>
                            <h2 style={{ marginBottom: "1rem" }}>Lern Modus auswählen</h2>
                            <div>
                                <TextField
                                    id="outlined-number"
                                    label="Anzahl Fragen"
                                    type="number"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    value={count}
                                    variant="outlined"
                                    onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                        setCount(parseInt(event.target.value));
                                    }}
                                />
                                <div className="fileUpload">
                                    <div className="name">Dateiname: {fileName}</div>
                                    <Button variant="contained" component="label" onChange={importJSON}>
                                        Datei auswählen
                                        <input type="file" hidden id="file-upload" />
                                    </Button>
                                </div>
                            </div>
                        </Paper>
                        <Paper elevation={3}>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={() => {
                                    if (questions.length > 0) setTestStarted(true);
                                    else {
                                        toast.warning("Du muss noch eine Datei mit Fragen importieren!");
                                    }
                                }}
                            >
                                Test starten
                            </Button>
                        </Paper>
                    </>
                )}
            </div>
        </div>
    );
}

export default App;
