import React from "react";

interface ParsedTextProps {
    children: string;
}
const ParsedText = ({ children }: ParsedTextProps) => {
    const re = /img([^)]*)\)/;
    const words = children.split(re);

    return (
        <div className="parsedText">
            {words.map((word, index) => {
                if (index % 2 === 0) {
                    return <div key={index}>{word}</div>;
                } else if (index % 2 === 1) {
                    return <img key={index} src={word.substring(1, word.length)} />;
                }
            })}
        </div>
    );
};

export default ParsedText;
